@font-face {
	font-family: "Panton";
	src: local("Panton"), url("../fonts/panton.ttf") format("truetype"), url("../fonts/panton.woff") format("woff"), url("../fonts/panton.woff2") format("woff2");
	font-weight: normal;
	font-style: normal;
}

@font-face {
	font-family: "Panton Semibold";
	src: local("Panton Semibold"), url("../fonts/panton-semibold.ttf") format("truetype"), url("../fonts/panton-semibold.woff") format("woff"), url("../fonts/panton-semibold.woff2") format("woff2");
	font-weight: normal;
	font-style: normal;
}

@font-face {
	font-family: "Helveticaneuecyr Roman";
	src: local("Helveticaneuecyr Roman"), url("../fonts/helveticaneuecyr-roman.ttf") format("truetype"), url("../fonts/helveticaneuecyr-roman.woff") format("woff"), url("../fonts/helveticaneuecyr-roman.woff2") format("woff2");
	font-weight: normal;
	font-style: normal;
}

body {
	font-family: "Helveticaneuecyr Roman", Arial, sans-serif;
	font-size: 16px;
	line-height: 21px;
	color: #646464;
	font-weight: normal;
}

.btn {
	display: inline-block;
	cursor: pointer;
	text-align: center;
	vertical-align: top;
	text-decoration: none;
	font-family: "Helveticaneuecyr Roman", Arial, sans-serif;
	font-size: 16px;
	line-height: 21px;
	color: white;
	font-weight: normal;
	background: linear-gradient(#feab16, #ff7500) #ff9000;
	padding: 10px 30px;
	border-radius: 4px;
	border: 2px solid #ff9000;
	transition: all 0.3s ease;
	box-shadow: 0px 0px 3px 1px #D2D2D2;
}

.btn:hover {
	background: #ff9451;
}

.btn:active {
	background: #f5843e;
}

.btn--call {
	float: right;
	max-width: 210px;
}

.btn--features {
	min-width: 155px;
}

input[type="text"],
textarea,
select,
input[type="password"] {
	box-sizing: border-box;
	width: 100%;
	outline: none;
	font-family: "Helveticaneuecyr Roman", Arial, sans-serif;
	font-size: 18px;
	line-height: 20px;
}

label {
	cursor: pointer;
}

button {
	outline: none;
	cursor: pointer;
}

input::-webkit-input-placeholder {
	opacity: 0.8;
}

input::-moz-placeholde {
	opacity: 0.8;
}

/*=== ╨║╨╛╨╝╨┐╨╛╨╜╨╡╨╜╤В╤Л ===*/

.header {
	padding: 30px 0;
}

.logo {
	float: left;
	width: 113px;
}

.logo img {
	display: block;
}

.menu {
	float: left;
	width: 710px;
	margin-left: 60px;
}

.menu__list {
	padding: 0;
	list-style: none;
	font-size: 0;
	margin-top: 10px;
}

.menu__item {
	display: inline-block;
	vertical-align: top;
	margin-right: 54px;
}

.menu__item:last-child {
	margin-right: 0;
}

.menu__link {
	font-family: "Helveticaneuecyr Roman", Arial, sans-serif;
	font-size: 16px;
	line-height: 21px;
	color: #192d44;
	font-weight: normal;
	text-decoration: none;
	padding-bottom: 5px;
}

.menu__link:hover {
	border-bottom: 1px solid #192d44;
}

.promo {
	width: 100%;
	height: 954px;
	background-image: url("../img/promo-bg.jpg");
	background-repeat: no-repeat;
	background-position: center;
	background-size: cover;
	box-sizing: border-box;
	padding-top: 45px;
	position: relative;
}

.promo__link {
	font-size: 0;
	background: url("../img/scroll.png") no-repeat;
	width: 33px;
	height: 48px;
	display: block;
	margin: 0 auto;
}

.promo-content {
	position: relative;
	margin-bottom: 300px;
}

.promo-content:after {
	content: "";
	display: table;
	clear: both;
}

.promo-content__image {
	width: 702px;
	height: 245px;
	float: right;
}

.promo-content__image img {
	display: block;
}

.promo-content__title {
	font-size: 24px;
	line-height: 27px;
	font-weight: normal;
	color: white;
	position: absolute;
	bottom: 5px;
	right: 25px;
	text-shadow: -1px 1px 2px #232323;
}

.features-block {
	padding-bottom: 115px;
}

.features__title {
	font-family: "Panton", Arial, sans-serif;
	font-size: 50px;
	line-height: 50px;
	color: #ff9000;
	transform: translateY(-20px);
	margin-bottom: 45px;
}

.features {
	font-size: 0;
}

.feature {
	font-family: "Helveticaneuecyr Roman", Arial, sans-serif;
	font-size: 17px;
	line-height: 21px;
	display: inline-block;
	vertical-align: top;
	width: 260px;
	margin-right: 40px;
}

.feature:last-child {
	margin-right: 0;
}

.feature__title {
	font-family: "Helveticaneuecyr Roman", Arial, sans-serif;
	font-size: 25px;
	line-height: 29px;
	color: #ff9000;
	margin: 0;
	margin: 40px 0;
	font-weight: normal;
}

.feature:before {
	content: "";
	width: 100px;
	height: 85px;
	background: #646464;
	display: block;
}

.feature__tip {
	color: #818181;
}

.feature__description {
	margin: 0;
	margin-top: 15px;
	margin-bottom: 25px;
}

.slider {
	background-image: url("../img/slider-bg.jpg");
	background-repeat: no-repeat;
	background-position: center;
	background-size: cover;
	position: relative;
}

.slider__controls {
	position: absolute;
	bottom: 25px;
	transform: translateX(-50%);
	left: 50%;
	font-size: 0;
}

.slider__control {
	width: 13px;
	height: 7px;
	background: #e8e8e8;
	margin-right: 10px;
	display: inline-block;
	vertical-align: top;
	border-radius: 1px;
}

.slider__control:last-child {
	margin-right: 0;
}

.slider__control--active {
	background: linear-gradient(#feab16, #ff7500) #ff9000;
}

.slider__title {
	font-family: "Panton", Arial, sans-serif;
	font-size: 30px;
	line-height: 15px;
	color: #e7e7e7;
	font-weight: normal;
	width: 732px;
	margin: 0;
	margin-top: 35px;
	margin-bottom: 20px;
}

.slider__description {
	color: #b0b0b0;
	font-size: 20px;
	line-height: 25px;
	width: 732px;
	margin: 0;
}

.slide {
	padding: 60px 0;
	position: relative;
}

.slide:after {
	content: "";
	display: block;
	position: absolute;
	bottom: 0;
	right: 0;
}

.slide--1:after {
	background: url("../img/slider-img-1.png") no-repeat;
	width: 310px;
	height: 399px;
}

.slide--2 {
	display: none;
}

.slide--2:after {
	background: url("../img/slider-img-2.png") no-repeat;
	width: 279px;
	height: 409px;
}

.slide--3 {
	display: none;
}

.slide--3:after {
	background: url("../img/slider-img-3.png") no-repeat;
	width: 332px;
	height: 418px;
}

/*=== ╤Б╤В╤А╨░╨╜╨╕╤Ж╤Л ===*/

body {
	min-width: 1200px;
	margin: 0;
	padding: 0;
	background: white;
}

.container {
	width: 1160px;
	margin: 0 auto;
	padding: 0 20px;
}

.container:after {
	content: "";
	display: table;
	clear: both;
}

.container--slider {
	padding: 0 80px;
	width: 1040px;
}

